export default {
	props: {
		likeItem: {
			type: Object,
			default () {
				return {}
			}
		}
	},
	
	data() {
		return {
			likeType: '',
			isMusicType: false,
			type: 0,
			id: 0
		}
	},
	
	created() {
		let { id, type } = this.likeItem
		let likeType = '电影'
		let isMusicType = false
		
		switch (type) {
			case 100:
				likeType = '电影'
				break
			case 200:
				likeType = '音乐'
				isMusicType = true
				break
			case 300:
				likeType = '诗句'
				break
		}
		
		this.likeType = likeType
		this.isMusicType = isMusicType
		this.type = type
		this.id = id
	},

	methods: {
		onLikeDetail(e) {
			let {type, id} = e.currentTarget.dataset
			uni.navigateTo({
				url: `/pages/like-detail/like-detail?type=${type}&id=${id}`
			});
		}
	}
}
